import "./index.sass"
import {createRoot} from "react-dom/client"
import {lazy, Suspense} from "react";
import Preloader from "./Components/Preloader/Preloader";
import {TagProvider} from "./Components/Tag/Tag";

const HomePage = lazy(() => import('./Pages/HomePage/HomePage'))

export let GLOBAL_PIXEL_SIZE = window.innerHeight / 786

const rootElement = document.getElementById("root")
if (rootElement) {
    const root = createRoot(rootElement)
    root.render(<>
        <Suspense fallback={<Preloader style={{width: "100vw", height: "100vh"}}/>}>
            <TagProvider tags={[
                {name: "", isTurnedOn: false},
                {name: " ", isTurnedOn: false},
                {name: " ", isTurnedOn: false},
                {name: "", isTurnedOn: false},
                {name: "", isTurnedOn: false},
                {name: "  ", isTurnedOn: false},
                {name: "Tik tok", isTurnedOn: false},
                {name: "Instagram", isTurnedOn: false},
                {name: "SEO", isTurnedOn: false},
                {name: "UI/UX", isTurnedOn: false}
            ]}>
                <HomePage/>
            </TagProvider>
        </Suspense>
    </>)
}