import React from "react";
import "./Preloader.sass"
import icon from "./images/preloader.svg"

interface IPreloaderProps {
    style?: React.CSSProperties
    className?: string
}

const Preloader: React.FC<IPreloaderProps> = ({style, className}) => {
    return <div className={["preloader", className].join(" ")} style={style}>
        <img className="preloader__icon" src={icon} alt="Preloader icon"/>
    </div>
}

export default Preloader