import React, {ButtonHTMLAttributes, createContext, useCallback, useContext, useEffect, useRef, useState} from "react";
import "./Tag.sass"
import {useSwiper, SwiperSlide} from "swiper/react"
import {IAnimated, useJustAnimation} from "../Animation/Animation";

type TagType = { name: string, isTurnedOn: boolean }

export const TagContext = createContext<{
    list: Array<TagType> | null,
    toggleTag: ((tagName: string, state: boolean) => void) | null
    getIsToggledTag: ((tagName: string) => boolean) | null
}>({list: null, toggleTag: null, getIsToggledTag: null})

export const TagProvider: React.FC<{ tags: Array<TagType>, children: React.ReactNode }> = ({tags, children}) => {
    const [tagsList, setTagsList] = useState<Array<TagType>>(tags)

    const toggleTag = useCallback((tagName: string, state: boolean) => {
        const tagIndex = tagsList.findIndex(elem => elem.name === tagName)
        if (tagIndex !== -1)
            setTagsList(old => {
                old[tagIndex].isTurnedOn = state
                return [...old]
            })
    }, [tagsList])

    const getIsToggledTag = useCallback((tagName: string) => {
        const tagIndex = tagsList.findIndex(elem => elem.name === tagName)
        if (tagIndex !== -1)
            return tagsList[tagIndex].isTurnedOn
        else return false
    }, [tagsList])

    return <TagContext.Provider value={{list: tagsList, toggleTag, getIsToggledTag}}>
        {children}
    </TagContext.Provider>
}

interface ITagProps extends ButtonHTMLAttributes<HTMLButtonElement> {
    children: string,
    iconSrc?: string,
    size?: "small" | "medium"
}

const Tag: React.FC<ITagProps & IAnimated> =
    ({
         animationOrder,
         doStartAnimation = false,
         animationParams,
         children,
         style,
         className,
         iconSrc,
         size,
         deps = []
     }) => {
        const [isTurnedOn, setIsTurnedOn] = useState(false)
        const inputRef = useRef<any>()
        const {toggleTag, getIsToggledTag, list} = useContext(TagContext)

        useEffect(() => {
            if (toggleTag) {
                toggleTag(children, isTurnedOn)
            }
        }, [isTurnedOn])

        useEffect(() => {
            if (list && getIsToggledTag) {
                setIsTurnedOn(getIsToggledTag(children))
            }
        }, [list])

        //Animation
        const containerRef = useRef<HTMLLabelElement>(null)
        const justAnimationStyles = useJustAnimation({
            doStartAnimation,
            objectRef: containerRef,
            animationOrder,
            animationParams,
            deps
        })

        return <label ref={containerRef} style={{...style, ...justAnimationStyles}} className={[
            "tag",
            className,
            isTurnedOn ? "tag_turned-on" : null,
            size === "medium" ? "tag_medium" : null
        ].join(" ")}>
            <input
                ref={inputRef}
                className="tag__checkbox"
                type="checkbox"
                checked={isTurnedOn}
                onChange={event => setIsTurnedOn(event.target.checked)}/>
            {children}
            {iconSrc ? <span className="tag__icon-place">
                <img src={iconSrc} alt="" className="tag__icon"/>
            </span> : null}
        </label>
    }

export default Tag

interface ITagsSetProps {
    style?: React.CSSProperties,
    className?: string,
    swiperSlideClassName?: string,
    size?: "small" | "medium"
}

export const TagsSet: React.FC<ITagsSetProps> = ({size, style, className, swiperSlideClassName}) => {
    const {list} = useContext(TagContext)
    const swiper = useSwiper()

    return <>
        {list ? list.map(tag => {
            return swiper
                ?
                <SwiperSlide key={tag.name} className={swiperSlideClassName}>
                    <Tag style={style} className={className}>{tag.name}</Tag>
                </SwiperSlide>
                :
                <Tag size={size} key={tag.name} style={style} className={className}>{tag.name}</Tag>
        }) : null}
    </>
}

export const useTagsList = () => {
    const [newList, setNewList] = useState<Array<string>>()
    const {list} = useContext(TagContext)

    useEffect(() => {
        if (list)
            setNewList(list.map(elem => elem.name))
    }, [list])

    return newList ? newList : null
}

export const useSelectedTagsList = () => {
    const [newList, setNewList] = useState<Array<string>>()
    const {list} = useContext(TagContext)

    useEffect(() => {
        if (list)
            setNewList(list.filter(elem => elem.isTurnedOn).map(elem => elem.name))
    }, [list])

    return newList ? newList : null
}