import React, {useEffect, useRef} from "react";
import anime, {AnimeInstance} from "animejs";


//Text animation word

interface ITextAnimationWordProps {
    children: string,
    delay?: number,
    deps?: Array<any>
}

const TextAnimationWord: React.FC<ITextAnimationWordProps> = ({children, delay = 0, deps = []}) => {
    const wordRef = useRef<HTMLSpanElement>(null)

    useEffect(() => {
        if (wordRef.current) {
            anime({
                targets: [wordRef.current],
                scaleY: [0, 1],
                easing: "cubicBezier(0,.4,.4,1)",
                duration: 500,
                opacity: [0, 1],
                delay
            })
        }
    }, deps)

    return <span style={{display: "inline-block", transformOrigin: "50% 100%", opacity: 0, transform: "scaleY(0)"}} ref={wordRef}>
            {children}
    </span>
}


//Text animation

interface ITextAnimationProps {
    children: string,
    deps?: Array<any>,
    delay?: number
}

const Animation: React.FC<ITextAnimationProps> = ({children, deps = [], delay = 0}) => {
    return <>{
        children
            .split(" ")
            // .map((word, index, arr) => arr.length !== index ? word + " " : word)
            .map((word, index) => {
                return <><TextAnimationWord key={index} deps={deps} delay={index * 80 + delay * 100}>{word}</TextAnimationWord> </>
            })
    }</>
}

export default Animation


//Just animation

interface IUseJustAnimation {
    doStartAnimation?: boolean,
    objectRef: React.RefObject<HTMLElement>,
    animationOrder?: number,
    deps?: Array<any>,
    animationParams?: {
        scale?: [number | string, number | string],
        scaleX?: [number | string, number | string],
        scaleY?: [number | string, number | string],
        translateX?: [number | string, number | string],
        translateY?: [number | string, number | string],
    }
}

export const useJustAnimation =
    ({
         doStartAnimation = false,
         deps = [],
         objectRef,
         animationOrder = 0,
         animationParams = {}
     }: IUseJustAnimation) => {
        const animeRef = useRef<AnimeInstance>()

        useEffect(() => {
            if (doStartAnimation) {
                animeRef.current = anime({
                    targets: [objectRef.current],
                    scale: [0.9, 1],
                    opacity: [0, 1],
                    easing: "cubicBezier(0,.4,.4,1)",
                    duration: 500,
                    delay: animationOrder * 100,
                    autoplay: false,
                    ...animationParams
                })

                if (objectRef && animeRef.current) {
                    animeRef.current.pause()
                    animeRef.current.play()
                }
            }
        }, [...deps, doStartAnimation])

        const outStyles: React.CSSProperties = doStartAnimation ? {opacity: 0, scale: 0.9} : {}

        return outStyles
    }

export interface IAnimated {
    doStartAnimation?: boolean,
    animationOrder?: number,
    deps?: Array<any>,
    animationParams?: {
        scale?: [number | string, number | string],
        scaleX?: [number | string, number | string],
        scaleY?: [number | string, number | string],
        translateX?: [number | string, number | string],
        translateY?: [number | string, number | string],
    }
}